import React, { useState } from "react";
import style from "./Users.module.css";

let Paginator = ({
  totalUserCount,
  pageSize,
  onPageChanged,
  currentPage,
  portionSize = 10,
}) => {
  let pagesCount = Math.ceil(totalUserCount / pageSize);
  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }
  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftBorder = (portionNumber - 1) * portionSize + 1;
  let rightBorder = portionNumber * portionSize;
  return (
    <div className={style.paginatorWrapper}>
      {portionNumber > 1 && (
        <button
          className={style.paginateButton}
          onClick={() => {
            setPortionNumber(portionNumber - 1);
          }}
        >
          PREV
        </button>
      )}
      {pages
        .filter((p) => p >= leftBorder && p <= rightBorder)
        .map((item) => {
          return (
            <span
              onClick={() => {
                onPageChanged(item);
              }}
              className={
                (currentPage === item && style.selectedPage) || style.paginate
              }
              key={item}
            >
              {item}
            </span>
          );
        })}
      {portionCount > portionNumber && (
        <button
          className={style.paginateButton}
          onClick={() => {
            setPortionNumber(portionNumber + 1);
          }}
        >
          NEXT
        </button>
      )}
    </div>
  );
};
export default Paginator;
