import React from "react";
import ContentImage1 from "../../../miami.jpg";
import style from "./ProfileInfo.module.css";
import Preloader from "../../common/preloader/preloader";
import lookingJob from "../../../assets/images/reviewok.png";
import ignoreJob from "../../../assets/images/false.png";
import ProfileStatus from "./ProfileStatus";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import defaultImage from "../../../assets/images/eric-cantona-3.png";

const ProfileInfo = (props) => {
  if (!props.profile) {
    return <Preloader />;
  }
  return (
    <div className={style.wrap}>
      Main Content
      <div>
        <img src={ContentImage1}></img>
      </div>
      <div className={style.usercontent}>
        <img
          src={
            props.profile.photos.large
              ? props.profile.photos.large
              : defaultImage
          }
        />
        <div className={style.usercontent_job}>
          <div className={style.status}>
            Status:
            <ProfileStatusWithHooks
              status={props.status}
              updateStatus={props.updateStatus}
            />
          </div>
          <p>{props.profile.fullName}</p>
          <div className={style.lookingforjob}>
            <p>Looking for a job</p>
            <img
              src={
                props.profile.lookingForAJob == true ? lookingJob : ignoreJob
              }
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
  );
};
export default ProfileInfo;
