export let requiredCreater = () => {
  return (value) => {
    if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
      return "please type pass";
    }
    return undefined;
  };
};

export let maxLengthCreator = (maxLength) => {
  return (value) => {
    if (value && value.length > maxLength) {
      return `Max length is ${maxLength} symbols`;
    }

    return undefined;
  };
};
