import React from "react";
import Post from "./Post/Post";
import style from "./MyPosts.module.css";
import {
  maxLengthCreator,
  requiredCreater,
} from "../../utils/validator/validators";
import { Field, reduxForm } from "redux-form";
import { TextArea } from "../../common/FormsControl/TextArea";
import { Component } from "react";

const maxLength = maxLengthCreator(10);
const MyPosts = React.memo((props) => {
  /*  shouldComponentUpdate(nextProps, nextState) {
    return nextProps != this.props || nextState != this.state;
  } */ //  //

  let postsElements = props.posts.map((el) => {
    return <Post message={el.message} id={el.id} count={el.likesCount} />;
  });
  /* let newPostElement = React.createRef(); */

  const addNewPost = (data) => {
    if (data.newPost && props.isAuth) props.addPost(data.newPost);
  };
  return (
    <div className={style.PostsClass}>
      <h3>My Posts</h3>
      <div className={style.PostsClassItem}>
        <PostFormRedux onSubmit={addNewPost} />
      </div>
      <div className={style.posts}>{postsElements}</div>
    </div>
  );
});

const postForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field
        component={TextArea}
        name="newPost"
        placeholder="write your posts"
        validate={maxLength}
      />
      <button>Add Post</button>
    </form>
  );
};
const PostFormRedux = reduxForm({
  form: "addPostForm",
})(postForm);
export default MyPosts;
