import { profileApi, usersApi } from "../api/api";

const ADD_POST = "ADD-POST";
const DELETE_POST = "DELETE_POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
let initialState = {
  posts: [
    { id: 1, message: "Hi, how are you", likesCount: 10 },
    { id: 2, message: "It's my first post", likesCount: 15 },
    { id: 3, message: "It's my second post", likesCount: 7 },
  ],

  profile: null,
  status: "",
};
export let addPostActionCreator = (datas) => {
  return {
    type: ADD_POST,
    datas,
  };
};
export let deletePost = (id) => {
  return {
    type: DELETE_POST,
    id,
  };
};

export let setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE,
    profile,
  };
};
export let setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};
const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD-POST": {
      let newPost = {
        id: state.posts.length + 1,
        message: action.datas,
        likesCount: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
      };

      return { ...state, posts: [...state.posts, newPost] };
    }
    case DELETE_POST: {
      return {
        ...state,
        posts: state.posts.filter((p) => p.id != action.id),
      };
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      };
    }
    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    default:
      return state;
  }
};
export let setUserProfileThunk = (userId) => {
  return async (dispatch) => {
    let response = await usersApi.userProfile(userId);
    dispatch(setUserProfile(response.data));
  };
};
export let setStatusThunk = (userId) => {
  return async (dispatch) => {
    let response = await profileApi.getStatus(userId);
    dispatch(setStatus(response.data));
  };
};
export let updateStatusThunk = (status) => {
  return async (dispatch) => {
    let response = await profileApi.updateStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(setStatus(status));
    }
  };
};
export default profileReducer;
